import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { ChatComponent } from './chat/chat.component';
import { HomeComponent } from './home/home.component';
import { UserChatsComponent } from './user-chats/user-chats.component';
import { YoutubePlayerComponent } from './youtube-player/youtube-player.component';

const routes: Routes = [
  {path:'', component: HomeComponent},
  {path:'home', component: HomeComponent , pathMatch:'full'},
  {path:'chats/:id', component: ChatComponent, pathMatch:'full'},
  {path:'userChats', component: UserChatsComponent, pathMatch:'full'},
  {path:'youtubePlayer', component: YoutubePlayerComponent, pathMatch:'full'}

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
